.icon {
    border-radius: 100%;
    height: 100px;
    width: 100px;
    transition: 200ms all;
  }
  
  .icon:hover{
    filter: saturate(200%);
    transform: scale(1.1);
    cursor: pointer;
  }
  
  .icon img {
    height: 60%;
    width: 60%;
    margin: 20%;
  }
  
  .typeImage3 {
    background: #92BC2C;
    box-shadow: 0 0 20px #92BC2C;
  }
  
